  }
  .prices__list-item.item-list {
    display: grid;
    grid-template-areas: "task price time order";
    grid-template-columns: 3fr 1fr 1fr 1fr;
    justify-content: space-between;
    border: none;
    border-radius: 0;
    cursor: default;
