html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 14px;
  color: #1b1c21;
  text-decoration: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.background {
  background-color: #f8f8f8;
}

.headline {
  position: relative;
}
.headline:not(.box__link-phone):before {
  position: absolute;
  content: "";
  left: -14px;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 40px;
  width: 4px;
  border-radius: 0px 10px 10px 0px;
  background-color: #41f6d7;
}
.headline.header__title:before {
  left: 2px;
}
@media (min-width: 1120px) {
  .headline.header__title:before {
    left: 4px;
  }
}

.subtitle {
  display: block;
  min-height: 56px;
  padding: 16px;
  font-family: "TT Lakes-Medium";
  font-size: 1.15em;
  line-height: 1.5em;
  letter-spacing: -0.2px;
  color: #7e7e82;
  font-weight: 100;
  text-transform: uppercase;
  background: linear-gradient(270deg, #fff 0%, #f8f8f8 100%);
